<template>
		<view class="flex-sub text-center">
			<view class="solid-bottom">
				<text class="text-yellow text-bold my-text text-shadow-info" 
					  :class="titleColor">{{ title }}</text>
			</view>
		</view>
</template>

<script>
	export default {
		name:"ImgTitle", 
		props:{
			avatarType:{
				type:[Number,String],
				default:1
			}, 
			title:{
				type:[Number,String],
				default:"1"
			}
		}, 
		computed:{
			titleColor(){
				const { avatarType } = this;
				const color = avatarType === 5 ? ['text-white'] : '';
				return color;
			}
		}
	}
</script>

<style lang="scss" scoped>
	.my-text {
		font-size: 40rpx;
	}
</style>
